import { NextRequest, NextResponse } from 'next/server';
import { query } from '@/lib/db';
import { verifyPasswordResetToken } from '@/lib/email';
import bcrypt from 'bcryptjs';

export async function POST(request: NextRequest) {
  try {
    const body = await request.json();
    const { token, newPassword } = body;

    if (!token || !newPassword) {
      return NextResponse.json(
        { error: '„Éà„Éº„ÇØ„É≥„Å®Êñ∞„Åó„ÅÑ„Éë„Çπ„ÉØ„Éº„Éâ„ÅåÂøÖË¶Å„Åß„Åô' },
        { status: 400 }
      );
    }

    if (newPassword.length < 6) {
      return NextResponse.json(
        { error: '„Éë„Çπ„ÉØ„Éº„Éâ„ÅØ6ÊñáÂ≠ó‰ª•‰∏ä„Åß„ÅÇ„ÇãÂøÖË¶Å„Åå„ÅÇ„Çä„Åæ„Åô' },
        { status: 400 }
      );
    }

    // „Éà„Éº„ÇØ„É≥Ê§úË®º
    const payload = verifyPasswordResetToken(token);
    
    if (!payload) {
      return NextResponse.json(
        { error: '„Éà„Éº„ÇØ„É≥„ÅåÁÑ°Âäπ„Åæ„Åü„ÅØÊúüÈôêÂàá„Çå„Åß„Åô' },
        { status: 400 }
      );
    }

    // Êñ∞„Åó„ÅÑ„Éë„Çπ„ÉØ„Éº„Éâ„Çí„Éè„ÉÉ„Ç∑„É•Âåñ
    const hashedPassword = await bcrypt.hash(newPassword, 10);

    // „Éë„Çπ„ÉØ„Éº„ÉâÊõ¥Êñ∞
    await query(
      'UPDATE users SET password_hash = $1 WHERE id = $2',
      [hashedPassword, payload.userId]
    );

    return NextResponse.json({
      message: '„Éë„Çπ„ÉØ„Éº„Éâ„ÅåÊ≠£Â∏∏„Å´„É™„Çª„ÉÉ„Éà„Åï„Çå„Åæ„Åó„Åü'
    });

  } catch (error) {
    console.error('Password reset error:', error);
    return NextResponse.json(
      { error: '„Éë„Çπ„ÉØ„Éº„Éâ„ÅÆ„É™„Çª„ÉÉ„Éà„Å´Â§±Êïó„Åó„Åæ„Åó„Åü' },
      { status: 500 }
    );
  }
}
