import jwt from 'jsonwebtoken';
import { query } from './db';

const JWT_SECRET = process.env.JWT_SECRET || 'fallback_secret_key';

// „É°„Éº„É´ÈÄÅ‰ø°Áî®„ÅÆ„Éà„Éº„ÇØ„É≥ÁîüÊàêÔºà„Éë„Çπ„ÉØ„Éº„Éâ„É™„Çª„ÉÉ„ÉàÁî®Ôºâ
export function generatePasswordResetToken(userId: number, email: string): string {
  return jwt.sign(
    { userId, email, type: 'password_reset' },
    JWT_SECRET,
    { expiresIn: '1h' } // 1ÊôÇÈñìÊúâÂäπ
  );
}

// „Éë„Çπ„ÉØ„Éº„Éâ„É™„Çª„ÉÉ„Éà„Éà„Éº„ÇØ„É≥„ÅÆÊ§úË®º
export function verifyPasswordResetToken(token: string): { userId: number; email: string } | null {
  try {
    const decoded = jwt.verify(token, JWT_SECRET) as any;
    if (decoded.type !== 'password_reset') {
      return null;
    }
    return { userId: decoded.userId, email: decoded.email };
  } catch {
    return null;
  }
}

